.container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  min-width: 50%;
  min-height: 100%;
  margin: 10px;
  background-color: white;
}

.container h1 {
  color: #222222;
  text-align: center;
  font-size: 5vh;
  margin: 0;
}

.container h2 {
  color: #222222;
  margin: 0;
  text-align: center;
  font-size: 3vh;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
  min-height: 100vh;
}

.container-selectors {
  border-right: 1px solid #dddddd;
}

.container-statistics {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  min-height: 100vh;
  flex: 4;
  border-left: 1px solid #dddddd;
}

.stat-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  min-width: 50%;
  min-height: 100%;
  margin-top: 50px;
  background-color: white;
}

.stat-column {
  display: flex;
  text-align: center;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
}

.stat-column h1 {
  font-size: 3vh;
}

.container-selectors a, li {
  text-decoration: underline;
  color: seagreen;
  font-size: 2vh;
}

.dropbox-container {
  position: relative;
  list-style: none;
}

.dropbox-list {
  overflow-y: scroll;
  max-height: 100vh;
  display: none;
  position: absolute;
  list-style: none;
  margin-top: 0;
}

.dropbox-container:hover {
  cursor: pointer;
}

.dropbox-container:hover .dropbox-list {
  display: block;
  cursor: pointer;
}

